body {
  background-color: #fafafa;
  font-family: Montserrat, sans-serif;
  font-size: 18px;
  line-height: 150%;
}

main.container {
  margin-top: 1rem;
}

.section {
  padding: 6em 1.5em 3em 1.5em;
}

@media screen and (max-width: 768px) {
  .section {
    padding-top: 4.5em;
  }
}

.no-horizontal-padding > .column {
  padding-top: 0;
  padding-bottom: 0;
}

p {
  margin-bottom: 16px;
}

a {
  color: rgba(50, 50, 50, 0.6);
}

.textarea {
  resize: none;
}

input,select,textarea {
  font-family: 'Montserrat';
}

select {
  padding-bottom: 0;
  padding-top: 0;
}

.input,.textarea {
  padding: 1.5em 1em;
  resize: none;
}

@media screen and (max-width: 768px) {
  .input,  .textarea {
    z-index: 10;
    background-color: rgba(242, 242, 242, 0.7);
  }
}

.select {
  align-items: center;
  display: flex;
}

.fade-in div,.fade-in span,.fade-in img,.fade-in a,.fade-in h4 {
  opacity: 1;
  transition: all 300ms ease-in-out;
}

.fade-out div,.fade-out span,.fade-out img,.fade-out a,.fade-out h4 {
  opacity: 0;
  transition: all 300ms ease-in-out;
}

.slide-left-out div,.slide-left-out span,.slide-left-out img,.slide-left-out a,.slide-left-out h4 {
  display: block;
  transform: translateX(-100%);
  transition: all 300ms ease-in-out;
}

.slide-right-out div,.slide-right-out span,.slide-right-out img,.slide-right-out a,.slide-right-out h4 {
  display: block;
  transform: translateX(100%);
  transition: all 300ms ease-in-out;
}

.slide-down-out div,.slide-down-out span,.slide-down-out img,.slide-down-out a,.slide-down-out h4 {
  display: block;
  transform: translateY(100%);
  transition: all 300ms ease-in-out;
}

.slide-in div,.slide-in span,.slide-in img,.slide-in a,.slide-in h4 {
  display: block;
  transform: translate(0, 0);
  transition: all 300ms ease-in-out;
}

.scale-out div,.scale-out img,.scale-out a {
  transform: scale(0.5);
  transition: all 300ms ease-in-out;
}

.scale-in img,.scale-in a {
  transform: scale(1);
  transition: all 300ms ease-in-out;
}

#home {
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

@media screen and (max-width: 768px) {
  #home {
    background-size: auto 100vh;
  }
}

.home-cta-title,.home-cta-tagline {
  font-family: 'Apertura';
  text-transform: lowercase;
}

.button-cta {
  height: 3em;
  font-weight: 800;
  text-transform: uppercase;
}

.button-cta svg {
  margin-left: 0.5em;
}